<template>
  <div class="main">
    <div class="row">
      <div class="container coursePageAll">
        <div class="coursePageTitle">
          <h3>加入課程</h3>
        </div>
        <Header />
        <div class="addClass_student">
          <div class="container">
            <div class="addClassInput">
              <label>輸入課程代碼</label>
              <input
                type="text"
                placeholder="請輸入課程代碼"
                v-model="Keyword"
              />
              <div class="submitBtn">
                <a href="javascript:void(0);" @click="search">搜尋</a>
              </div>
            </div>
            <div class="tableStyle1" v-if="ClassRoom">
              <table>
                <tr>
                  <th>開課期間</th>
                  <th>教師</th>
                  <th width="25%">課程主題</th>
                  <th width="25%">課程內容</th>
                  <th>作業狀態</th>
                  <th>備註</th>
                </tr>
                <tr>
                  <td rowspan="2">
                    {{ ClassRoom.StartTime | dateFormat }}~{{
                      ClassRoom.EndTime | dateFormat
                    }}
                  </td>
                  <td rowspan="2">{{ Teacher.Name }}</td>
                  <td rowspan="2">
                    {{ ClassRoom.LessonTitle }}<br />({{ ClassRoom.Code }})
                  </td>
                  <td class="s_courseLink">
                    <router-link
                      :to="'/lesson/' + Lessons[0].LessonID"
                      v-if="Lessons.length > 0"
                      >{{ Lessons[0].LessonName }}</router-link
                    >
                  </td>
                  <td class="s_courseLink" v-if="!ClassRoom.HasHomeWork"></td>
                  <td class="s_courseLink" v-else>
                    <template v-if="Lessons[0].HomeWork">
                      <span v-if="Lessons[0].HomeWork.Answers">
                        {{ Lessons[0].HomeWork.CorrectCount }}/{{
                          Lessons[0].HomeWork.TotalCount
                        }}
                      </span>
                      <span v-else-if="ClassRoom.Expired">未開課</span>
                      <router-link
                        v-else
                        :to="'/homework/' + Lessons[0].LessonID"
                        >未完成</router-link
                      >
                    </template>
                  </td>
                  <td rowspan="2">
                    <a
                      class="addToMyClass"
                      href="javascript:void(0)"
                      @click="add"
                      v-if="!Added"
                      >加入課程</a
                    >
                    <p v-else>已加入</p>
                  </td>
                </tr>
                <tr v-for="(l, i) in Lessons" :key="l.LessonID" v-show="i > 0">
                  <td class="s_courseLink">
                    <router-link :to="'/lesson/' + l.LessonID">{{
                      l.LessonName
                    }}</router-link>
                  </td>
                  <td class="s_courseLink" v-if="!ClassRoom.HasHomeWork"></td>
                  <td class="s_courseLink" v-else>
                    <template v-if="l.HomeWork">
                      <span v-if="l.HomeWork.Answers">
                        {{ l.HomeWork.CorrectCount }}/{{
                          l.HomeWork.TotalCount
                        }}
                      </span>
                      <span v-else-if="ClassRoom.Expired">未開課</span>
                      <router-link v-else :to="'/homework/' + l.LessonID"
                        >未完成</router-link
                      >
                    </template>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "@/components/Student/Header.vue";
export default {
  components: {
    Header,
  },
  data() {
    return {
      Keyword: "",
      ClassRoom: null,
      Teacher: null,
      Lessons: [],
      Added: false,
    };
  },
  methods: {
    async search() {
      if (!this.Keyword) {
        return;
      }
      try {
        var response = await this.$api.getClassRoomByCode(this.Keyword);
        this.ClassRoom = response.ClassRoom;
        this.Teacher = response.Teacher;
        var map = {};
        response.HomeWorks.forEach((h) => (map[h.LessonID] = h));
        response.Lessons.forEach((l) => {
          l.HomeWork = map[l.LessonID];
        });
        this.Lessons = response.Lessons;
        this.Added = response.Added;
      } catch (e) {
        alert("找不到此課程");
      }
    },
    async add() {
      try {
        await this.$api.addMyClassRoom(this.ClassRoom.ClassRoomID);
        this.$router.push("/student/class/my");
      } catch (e) {
        if (e.Status == 6) {
          alert("未開課");
        }
      }
    },
  },
};
</script>